// 引入插件
import VueRouter from "vue-router";
//引入需要路有的组件
import MyIndex from "@/components/IndexPage/MyIndex";
import MyDetails from "@/components/DetailsPage/MyDetails";
import FriendDetialsPage from "@/components/FriendLink/FriendDetialsPage";

import MyAdmin from "@/components/SystemAdmin/MyAdmin";
import CommentFact from "@/components/SystemAdmin/ManageFact/CommentFact";
import ArticleFact from "@/components/SystemAdmin/ManageFact/ArticleFact";
import UserStatus from "@/utils/userStatus";
import AboutPage from "@/components/AboutPage/AboutPage";
import MessageFact from "@/components/SystemAdmin/ManageFact/MessageFact";
import LinkFact from "@/components/SystemAdmin/ManageFact/LinkFact";
import TagFact from "@/components/SystemAdmin/ManageFact/TagFact";
import BoutiqueFact from "@/components/SystemAdmin/ManageFact/BoutiqueFact";
import TimelineFileFact from "@/components/SystemAdmin/ManageFact/TimelineFileFact";
import TimelineFileUser from "@/components/TimelineFilePage/TimelineFileUser";
import BoutiqueUser from "@/components/BoutiquePage/BoutiqueUser";
import SortUser from "@/components/SortPage/SortIndex/SortUser";
import SearchUser from "@/components/SearchPage/SearchUser";
import SortDetailsPage from "@/components/SortPage/SortDetails/SortDetailsPage";
import MyLogins from "@/components/login/MyLogins";
import userStatus from "@/utils/userStatus";
import axios from "axios";
import {ApiOrigin} from "@/config/ApiOrigin";
import Status404 from "@/components/Err/Status404";
import Status500 from "@/components/Err/Status500";
import statusNoConnection from "@/components/Err/StatusNoConnection";
//创建路由器
const MyRouter = new VueRouter({
    routes: [
        {
            name: "index",
            path: "/",
            component: MyIndex,
            meta: {title: "幻梦博客"}
        },
        {
            name: "jh",
            path: "/Boutique",
            component: BoutiqueUser,
            meta: {title: "本站精华"},
        },
        {
            name: "sort",
            path: "/Sort",
            component: SortUser,
            meta: {title: "分类索引"},
        },
        {
            name: "sortDetails",
            path: "/SortPage/:tag",
            component: SortDetailsPage,
            meta: {title: "分类页"},
        },
        {
            name: "file",
            path: "/TimelineFile",
            component: TimelineFileUser,
            meta: {title: "文章归档"},
        },
        {
            name: "link",
            path: "/MyLink",
            component: FriendDetialsPage,
            meta: {title: "友情链接"}
        },
        {
            name: "about",
            path: "/About",
            component: AboutPage,
            meta: {title: "关于本站"},
        },
        {
            name: "login",
            path: "/Logins",
            component: MyLogins,
            //配置是否需要进行权限校验
            meta: {isAuth: true, title: "登陆注册一体机"},
        },

        {
            name: "details",
            path: "/details/:title",
            component: MyDetails,
            meta: {title: "文章详情"}
        },

        {
            name: "search",
            path: "/Search/:search",
            component: SearchUser,
            meta: {title: "全站搜索"}
        },
        {
            name: "404",
            path: "*",
            component: Status404,
            meta: {title: "404"}
        },
        {
            name: "500",
            path: "/500",
            component: Status500,
            meta: {title: "500"}
        },
        {
            name: "noconn",
            path: "/NOTCONNECTION",
            component: statusNoConnection,
            meta: {title: "无网络连接"}
        },
        {
            //只有一个孩子时，必须要加的
            alwaysShow: true,
            name: "admin",
            path: "/Admin",
            component: MyAdmin,
            //配置是否需要进行权限校验
            meta: {isAuth: true, title: "博客后台"},
            children: [
                {
                    name: "wzM",
                    path: "/Admin/Article",
                    component: ArticleFact,
                    meta: {isAuth: true, title: "文章管理"},
                },
                {
                    name: "plM",
                    path: "/Admin/Comment",
                    component: CommentFact,
                    meta: {isAuth: true, title: "评论管理"},
                },
                {
                    name: "lyM",
                    path: "/Admin/Message",
                    component: MessageFact,
                    meta: {isAuth: true, title: "留言管理"},
                },
                {
                    name: "ylM",
                    path: "/Admin/FriendLink",
                    component: LinkFact,
                    meta: {isAuth: true, title: "友链管理"},
                },
                {
                    name: "bqM",
                    path: "/Admin/Tag",
                    component: TagFact,
                    meta: {isAuth: true, title: "标签管理"},
                }, {
                    name: "jhM",
                    path: "/Admin/Boutique",
                    component: BoutiqueFact,
                    meta: {isAuth: true, title: "精华管理"},
                },
                {
                    name: "gdM",
                    path: "/Admin/TimelineFile",
                    component: TimelineFileFact,
                    meta: {isAuth: true, title: "归档管理"},
                }
            ]
        }
    ]
})
// 解决：Uncaught (in promise) NavigationDuplicated;
// 先保存一份 VueRouter.prototype.push方法
let originalPush = VueRouter.prototype.push;
let originalRepace = VueRouter.prototype.replace;

VueRouter.prototype.push = function push(location) {
    return originalPush
        // call(); 篡改上下文
        .call(this, location)
        // catch(); 捕获异常 ****
        .catch(e => e);
}
VueRouter.prototype.replace = function replace(location) {
    return originalRepace.call(this, location).catch(e => e);
}
// MyRouter.mode="history"
//定义需要携带请求头token的数组


//前置路由守卫,和后端的过滤器有点像
MyRouter.beforeEach((to, from, next) => {

    let isA = null
    try {
        isA=to.meta.isAuth
    }
    catch (e) {
        isA=null
    }
    if (isA != null) {
        if (isA) {
            // 统一设置Token请求头
            // 添加请求拦截器，在请求头中加token
            axios.interceptors.request.use(
                config => {
                    let token = userStatus.getData("token");
                    // 判断本地的cookie中是否有token
                    if (token) {
                        config.headers['token'] = token
                    }
                    // else {
                    //     if (to.name!=="login"){
                    //         alert("身份认证失效，请重新登陆！")
                    //     }
                    // }
                    return config
                },
                error => {
                    return Promise.reject(error)
                })
            next();
        }
    }
    //校验管理员是否合法
    if (to.name === "admin") {
        if (UserStatus.getUser("user") === "admin") {
            next();
        }
    } else {
        next();
    }

})

//后置路由守卫,实现网页标题联动效果
MyRouter.afterEach((to, from) => {
    document.title = to.meta.title || "幻梦博客"
})
export default MyRouter;
