import axios from "axios";
import MyRouter from "@/routers";
import AxiosStatus from "@/mixs/AxiosStatus";
import {ApiOrigin} from "@/config/ApiOrigin";
import userStatus from "@/utils/userStatus";

export default function AxiosHandle() {
    // //测试连接
    // axios.get(ApiOrigin+"/cs/get").then((e)=>{
    //     if(e.status!==200){
    //         MyRouter.push({name:"500"})
    //     }
    // }).catch(()=>{
    //     MyRouter.push({name:"500"})
    // })

    //响应结果拦截器
    axios.interceptors.response.use(response => {
        return response;
    }, error => {
        let code=error.code
        console.log(error)

        switch (code) {
            case AxiosStatus.ErrNoConnection:
                userStatus.removeUser("token")
                userStatus.removeUser("user")

                MyRouter.push({name: 'login'});
                break;
            case AxiosStatus.Err403:
                MyRouter.push({name: '500'});
                break;
        }
        return Promise.reject(error);
    });
}
